@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
html {
    --dark-teal: #1e262b;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.name {
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat';
}

.name__wrapper {
    display: flex;
    justify-content: center;
}

.name__banner {
    height: 16vw;
}

.name__promo {
    height: 16vw;
}

.name__whitespace {
    min-height: 30vh;
}

.social {
    margin: 3vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.social__wrapper {
    display: grid;
    grid-template-columns: calc(3.9vw + 20px) calc(3.9vw + 20px) calc(3.9vw + 20px); 
    grid-template-rows: calc(3.9vw + 20px);
    grid-column-gap: calc(2vw + 20px);
    grid-row-gap: 20px;
}

.social__icon {
    width: calc(3.9vw + 10px);
    transition: width 0.07s;
}

.social__icon:hover {
    width: calc(3.9vw + 20px);
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    --hover-transition-duration: 0.15s;
    --primary-bg: #FAFBFF;
    --secondary-bg: #F8F9FD;
    --invisible: #00000000;

    --blurhash-width: 300px;
    --blurhash-height: 270px;
}

.wrapper--85 { width: 85%; }
.wrapper--100 { width: 100%; }

.noselect {
  -webkit-touch-callout: none; 
    -webkit-user-select: none; 
        -ms-user-select: none; 
            user-select: none; 
}

.app {
    min-height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    background-color: var(--primary-bg);
}

.app__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.app__header {
    display: grid;
    align-items: center;
    width: 85%;
    height: 14vh; 
    grid-template-columns: 30% 30% 30%;
    grid-column-gap: 5%;
    margin: 2vh 0 2vmin 0;
}

.app__logo {
    width: calc(20px + 5vmax);
    margin-left: 2vw;
}

.app__discover {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Cantarell';
    font-size: calc(2vw + 20px);
}

.app__body {
    max-width: 100%;
}

.section {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 8vmin 0 8vh 0;
    overflow: hidden;
}

.section__wrapper {
    background-color: var(--secondary-bg);
    padding: 4vh;
    margin: 10px 0 10px 0;
    border: solid 1px #DADEDF;
    border-radius: 4vh;
    box-shadow: 1px 1px 5px #DFF0F5;
    transition: all var(--hover-transition-duration);
}

.section__wrapper:hover {
    box-shadow: 1px 1px 8px #DADEDF;
}

.section__header {
    font-family: 'Montserrat';
    font-size: calc(1vw + 10px);
    margin-bottom: 4vh;
}

.section__header--padding { margin-left: 35px; }

.section__body {}

.alice-carousel__dots { display: none !important; }
.alice-carousel__prev-btn { display: none !important; }
.alice-carousel__next-btn { display: none !important; }


.blurhash {
    width: var(--blurhash-width);
}

.blurhash--normal {
    -webkit-mask: url(/static/media/blurhash_mask.b8dbc159.svg);
            mask: url(/static/media/blurhash_mask.b8dbc159.svg);
    -webkit-mask-size: 100% 100%;
            mask-size: 100% 100%;
}

.blurhash--small {
    -webkit-mask: url(/static/media/blurhash_mask_small.f0595f13.svg);
            mask: url(/static/media/blurhash_mask_small.f0595f13.svg);
    -webkit-mask-size: 100% 100%;
            mask-size: 100% 100%;
}

.blurhash__mask { 
    width: var(--blurhash-width);
    position: absolute;
    z-index: 10;
}

.blurhash__mask--online {
    opacity: 0;
}

.blurhash__mask--offline {
    opacity: 0.4;    
}

.restaurant {
    min-width: calc(1vw + var(--blurhash-width));
    max-height: calc(0.6vmax + 30px + var(--blurhash-height));
    font-family: 'Cantarell';
    font-size: calc(0.6vmax + 7px);
    transition: all var(--hover-transition-duration);
}

.restaurant__title {
    display: grid;
    align-items: center;
    grid-template-rows: calc(0.6vmax + 30px);
}

.restaurant:hover { font-size: calc(0.6vmax + 13px); }

.main {
    min-height: 100vh;
    background-color: 'HoneyDew';
}



